
#app{
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
.container{
  max-width: 450px;
  margin: 150px auto;
  text-align: center;
}
.header{
  background:#42b983;
  padding: 1px;
}
.header h1{
  color: #fff;
  text-transform: uppercase;
  font-size: 35px;
}

form{
  border: 1px solid #f5f5f5;
}
.word{
  width: 100%;
  border: none;
  padding: 20px 0;
  text-align: center;
  text-transform: capitalize;
}
.select-wrap select {
  padding: 10px 8px;
  border: 1px solid #f5f5f5;
  width: 100%;
  border-radius: 0;
  -webkit-box-shadow: none;
          box-shadow: none;
  background: transparent;
  background-image: none;
  -webkit-appearance: none;
  text-align: center;
}
.button{
  cursor: pointer;
  width: 100%;
  border: none;
  background: #42b983;
  color: #fff;
  font-weight: bold;
  padding: 10px 8px;
  font-size: 20px;
}















